	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"","date":"23 Feb 2003","slug":"/2003-02-23-hast-du-etwas-zeit-fuer-mich/","tags":"Y2003"},"html":"<p>It's probably a sign of my age, or least of when I started taking a interest in German popular culture, that I noticed the <a title=\"20 Jahre Nena-Nena Feat.Nena\" href=\"http://www.amazon.de/exec/obidos/ASIN/B00006LWT0\">20th anniversary</a> album by Nena, consisting of new versions of most of her '80s hits, and thus entitled 'Nena feat. Nena'.  She still has the unique voice I'd follow almost anywhere, and the new version of <em>Leuchtturm</em> is quite good and has gotten some radio airplay, so I impulsively picked up the CD yesterday. <em>99 Luftballons</em> doesn't cut it as a ballad, the duets with Kim Wilde and Udo Lindenburg are a bit bizarre, and she ruined my favorite Nena song <em>Nur getr채umt</em> (but then again, so did Bl체mchen, or maybe the song isn't as good as I remember). But I'd forgotten how much I liked <em>Fragezeichen</em>, and all in all it's a nice nostalgia trip back to the '80s. To counteract the nostalgia, I also bought the <a title=\"200 Km/H in the Wrong Lane\" href=\"http://www.amazon.de/exec/obidos/ASIN/B00006LJ79\">t.A.T.u.</a> album, which is in fact better than I expected, even though I'm too old to be listening to teenie girl groups, even (or especially) if they are from Russia. But then it's produced the old producer of Frankie Goes To Hollywood, so it's really '80s retro. Sigh. There's no escape.</p>"},"next":{"fields":{"title":"At The Circus","date":"23 Feb 2003","slug":"/2003-02-23-at-the-circus/"}},"previous":{"fields":{"title":"You're So Vain","date":"26 Feb 2003","slug":"/2003-02-26-youre-so-vain/"}}},{"node":{"fields":{"title":"At The Circus","date":"23 Feb 2003","slug":"/2003-02-23-at-the-circus/","tags":"Y2003"},"html":"<p>Our entertainment today was the <a title=\"Circus Geschwister Rogall Artistik und Akrobatik aus Berlin\" href=\"http://www.circus-rogall.de/\">Circus Rogall</a>, a small family circus that was playing in Jesteburg. Christopher was skeptical at first, but he did enjoy the clown and the horses, as well as the popcorn. I was impressed by the admission price (double the price of a movie ticket), but also by the versatility of the troupe. Five performers put on the entire show, so the ringmaster was also the clown and the trapeze artist and the horse and camel trainer. Sounds like the life of a sysadmin. </p>\n<p>This evening we made pizza together, and Christopher actually did most of the work. He put the flour and water in the mixer, helped roll out the dough, poured on the tomato sauce, arranged the salami, and spread out the grated cheese. He then washed his hands for 20 minutes, which should have made me suspicious. Sure enough, he had spread sun lotion throughout the guest bathroom, on the walls and the fixtures, as well as in his hair.</p>\n<p>It's been a fun weekend with Christopher, but I'd still rather have Mama around.</p>"},"next":{"fields":{"title":"Mr. Christopher Goes To Town","date":"22 Feb 2003","slug":"/2003-02-22-mr-christopher-goes-to-town/"}},"previous":{"fields":{"title":"","date":"23 Feb 2003","slug":"/2003-02-23-hast-du-etwas-zeit-fuer-mich/"}}},{"node":{"fields":{"title":"Mr. Christopher Goes To Town","date":"22 Feb 2003","slug":"/2003-02-22-mr-christopher-goes-to-town/","tags":"Y2003"},"html":"<p>The adventure for Christopher and me today was a trip to Hamburg. We parked at Berliner Tor and took the U-Bahn to <a href=\"http://www.landungsbruecken.de/hamburg.htm\">Landungsbrucken</a>, the gateway to the <a href=\"http://www.hafen-hamburg.de/html-engl/home.htm\">Hamburg Harbor</a>. There I treated Cristopher to a harbour tour on the <a href=\"http://www.louisiana-star-hamburg.de/\">Louisiana Star</a>, and we drank capuccino and hot chocolate as we looked at the big container ships. We then went downtown and took a look at the home electronics store <a href=\"http://www.saturn.de/\">Saturn</a>. This was Christopher's first long outing without the buggy, so he walked the entire day and came home exhausted.</p>"},"next":{"fields":{"title":"Long Weekend","date":"21 Feb 2003","slug":"/2003-02-21-long-weekend/"}},"previous":{"fields":{"title":"At The Circus","date":"23 Feb 2003","slug":"/2003-02-23-at-the-circus/"}}}],"pathPrefix":"","first":false,"last":false,"index":784,"pageCount":1101}}

